.drawing-board-toolbar-container {
  display: flex;
  height: 55px;
  background: #ffffff;
  box-shadow: -3px 0px 13px 0px rgba(0, 0, 0, 0.08);
  border-radius: 3px 3px 0px 0px;
  padding-left: 20px;
  padding-right: 150px;
  position: relative;
  z-index: 1;
}
.drawing-board-toolbar-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 100%;
  cursor: pointer;
  margin: 0 7px;
}
.drawing-board-toolbar-icon svg {
  width: 20px;
  height: 20px;
  margin-bottom: 2px;
  fill: #1890ff;
}
.drawing-board-toolbar-icon:hover {
  background: #eeeeee;
}
.drawing-board-toolbar-iconLabel {
  cursor: pointer;
  font-size: 12px;
  font-weight: 400;
  line-height: 17px;
  user-select: none;
}
.drawing-board-toolbar-activeIcon {
  background: #eeeeee;
}
